@keyframes move-arrow {
  0% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }

  18% {
    opacity: 0;
    visibility: hidden;
  }

  33% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
  }

  77% {
    opacity: 0;
    visibility: hidden;
    transform: translateX(-100%);
  }

  100% {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }
}

