.scroll-effect {
  opacity: 0;
  transition: all .3s ease-in-out;
  transform: translateX(0);
}

.scroll-effect.left {
  transform: translateX(-99%);
}

.scroll-effect.right {
  transform: translateX(99%);
}

.scroll-effect.visible {
  opacity: 1;
  transform: translateX(0);
}

.scroll-effect--1 {
  transition-duration: .3s;
  transition-delay: 0s;
}

.scroll-effect--2 {
  transition-duration: .5s;
  transition-delay: .2s;
}

.scroll-effect--3 {
  transition-duration: 1s;
  transition-delay: .5s;
}

